.MoneyEx {
  @apply h-screen container mx-auto w-full sm:w-10/12 lg:w-11/12 xl:w-10/12 2xl:w-8/12;

  .gridWrap {
    @apply grid grid-cols-1 gap-3 sm:gap-6 2xl:gap-8;
    @apply mt-5 lg:mt-10;
  }

  .inputWrap {
    @apply relative;
    @apply bg-white shadow-lg;
    @apply bg-gradient-to-t from-neutral-100;
    @apply px-3 sm:px-5 py-3 lg:py-7;
    @apply lg:space-x-5;
    @apply flex flex-col lg:flex-row justify-around;
  }

  .inputItem {
    @apply first-of-type:pb-3 last-of-type:pt-3 lg:first-of-type:pb-0 lg:last-of-type:pt-0;
    @apply first-of-type:order-1 last-of-type:order-3;
    @apply text-lg sm:text-xl lg:text-xl font-semibold;
    @apply flex flex-row items-center;
  }

  .inputItemsSeparator {
    @apply lg:hidden;
    @apply first-of-type:order-2;
    @apply rounded-full bg-neutral-200 opacity-50;
    padding: 0.0625rem;
  }

  .inputUnitIconComponent {
    @apply mt-1 md:mt-0.5 sm:pr-2;
  }

  .inputItemCodeSymbol {
    @apply w-24 sm:w-28 xl:w-32 py-2 mr-2 text-center;
    word-spacing: -10px;
    @apply text-green-700 text-opacity-70;
    @apply font-mono;
  }

  .inputField {
    @apply outline-none w-full bg-transparent;
    @apply text-gray-400 font-bold tracking-wide;
    @apply focus:text-gray-600;
    @apply font-mono placeholder:text-gray-300;
    // @apply placeholder:text-base sm:placeholder:text-lg md:placeholder:text-xl;
    @apply placeholder:text-xs sm:placeholder:text-sm md:placeholder:text-base;
  }

  .clearInputIcon {
    @apply w-4 h-4 sm:w-6 sm:h-6 opacity-50;
    @apply hover:opacity-80;
    @apply active:opacity-80;
  }

  .swapIcon {
    @apply w-5 h-5 sm:w-6 sm:h-6 lg:w-8 lg:h-8;
    @apply opacity-100 lg:opacity-70 hover:opacity-100 hover:cursor-pointer;
    // @apply rotate-0 lg:rotate-90;
    @apply rotate-90 lg:rotate-0;
    @apply absolute z-10 lg:static lg:z-0;
    @apply self-center justify-center lg:first-of-type:order-2;
  }

  .favList {
    @apply grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3;
    @apply gap-5 xl:gap-7;
    @apply px-5 py-3 md:p-4;
    @apply rounded-lg;
    // @apply bg-gray-50 shadow-xl;
    @apply bg-transparent;

    .itemWrap {
      @apply py-1 rounded-lg bg-white shadow-lg shadow-neutral-300;
    }

    .favItemActionBtnGroup {
      @apply grid grid-cols-2;
      @apply mt-2 -mb-1;
    }

    .favItemActionBtn {
      font-size: 0.65rem;
      line-height: 0.75rem;
      @apply first:rounded-bl-lg border-r-2 last:border-r-0 last:rounded-br-lg p-1;
      @apply text-slate-400 tracking-widest;
      @apply bg-gradient-to-t from-neutral-200;
      @apply hover:bg-gradient-to-b hover:cursor-pointer;
      @apply active:bg-gradient-to-b active:cursor-pointer active:animate-bounce;
    }

    .itemInfoWrap {
      @apply flex flex-row;

      .favItemRightGroup {
        @apply flex flex-col justify-center;
      }
      .favItemSymbolValueGroup {
        word-spacing: -5px;
      }
      .name {
        @apply text-gray-400 text-xs sm:text-xs;
      }
      .favItemSymbol {
        @apply text-gray-400;
      }
      .exRate {
        @apply text-sm md:text-base font-bold text-gray-600 tracking-wide font-mono;
      }
      .codeFlagGroup {
        @apply h-12 w-24 rounded-xl bg-neutral-200 bg-opacity-50 shadow p-3 text-center scale-75;
        @apply flex flex-row justify-center;
        @apply text-base font-semibold tracking-wide text-gray-600;
      }
    }
  }

  .starColor {
    filter: brightness(1) grayscale(1);
    // filter: invert(48%) sepia(79%) saturate(2476%) hue-rotate(86deg) brightness(118%) contrast(119%);
  }
}
